*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
* {
  box-sizing: border-box;
}

body, html {
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* ðŸ’¥ Prevent horizontal scroll */
  overflow-y: hidden;
}

.app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

body{
  font-family: 'Times New Roman', Times, serif;
}
.app{
  background-image: url('./Assets/Warm.avif');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}
.app.warm{
  background-image: url('./Assets/Winter.avif');
}
main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
  padding: 25px;
}
.search-box{
  width: 100%;
  margin: 0 0 75px;

}
.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 16px 16px 16px 16px;
  margin-top: -15px;
  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
  font-family: Arial, Helvetica, sans-serif;
}
.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
}
.location-box .location{
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);

}
.location-box .date{
  color: #FFF;
  font-size: 20 px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgbs(50,50,70,0.5);

}
.weather-box{
  text-align: center;
}
.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: Black;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 2px 6px rgba(50,50,70,0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);

}
.weather-box .weather{
  color: white;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3p rgba(50,50,70,0.5);
}
.heading{
  border: 2px solid transparent;
  background-color: rgba(255,255,255,0.2);
  border-bottom: 0px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  font-size: 70px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: white;
  padding: 15px 20px;
  font-weight: 1000;
  white-space: nowrap;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
  display: inline-block;
  padding: 15px 25px;
  border-radius: 40px 40px 0px 0px;
  text-shadow: 2px 6px rgba(50,50,70,0.5);
  translate: transform 0.8s ease-in-out;

}
.heading:hover{
  transform: translateX(-50%) scale(1.05); 
}